.footer {
  display: -webkit-flex;
  display: flex;
  position: fixed;
  width: 100vw;
  background: #2180b3;
  height: 88px;
  bottom: 0;
}

.f {
  position: relative;
  margin-left: 70px;
  margin-right: 30px;
  margin-top: 30px;
  left: 20vw;
  color: #ffffff;
  font-family: Avenir Next;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  cursor: pointer;
}

/* Adding some  media query for Responsiveness */

@media only screen and (max-width: 992px) {
  .f {
    left: 10vw;
  }
}

